<template>
    <div>
        <form @submit.prevent="emailLogin">
            <input type="text" placeholder="Email or Phone Number" v-model="emailOrPhone" required />
            <br/>
            <input type="password" placeholder="Password" v-model="password" required />
            <button type="submit">Login</button>
        </form>
        <div>
            <!-- You can handle guest login here -->
            <button @click="guestLogin">Login as Guest</button>
        </div>

        <div>
            <!-- Google login button  -->
        </div>
    </div>
</template>
  
<script>
import axios from 'axios';

export default {
    data() {
        return {
            emailOrPhone: '',
            password: ''
        };
    },
    methods: {
        async emailLogin() {
            try {
                const payload = {
                    email: this.emailOrPhone,  // Use this if it's email
                    phone_number: this.emailOrPhone,  // Use this if it's phone number
                    username: 'someUsername',  // Replace with actual username
                    password: this.password,
                    login_type: 'email'  // Or phone
                };
                const response = await axios.post('http://localhost:8000/user/sign_up', payload);
                console.log('User signed up:', response.data);
            } catch (error) {
                console.error('An error occurred:', error);
            }
        },
        async guestLogin() {
            // Implement your guest login logic here
            console.log('Logged in as Guest');
        }
    }
};
</script>
  
